import { Link, useLocation, useNavigate } from 'react-router-dom'
import { Loader } from '../Components/Loader'
import { Message } from '../Components/Message'
import FormContainer from '../Components/FormContainer'
import { useContext, useState, useEffect, useRef } from 'react'
import axios from 'axios'
import { UserContext } from '../store/UserContext'
import styled from 'styled-components'
import { isAlphabetical, isAlphaNumerical } from '../utils'
import { BrowserContext } from '../store/BrowserContext'
import { TranslationContext } from '../store/TranslationContext'

const StyledSpan = styled.span`
margin-inline-start: 10px;
`

/////////////////////////
// COMPONENT
const RegisterScreen = () => {
  const [step, setStep] = useState(1)

  const nextHandler = (event)=>{
    event.preventDefault()
    setStep(2)
  }

  const prevHandler = (event)=>{
    event.preventDefault()
    setStep(1)
  }

  const [isNextDisabled, setIsNextDisabled] = useState(true)
  const [isRegistrationDisabled, setIsRegisrationDisabled] = useState(true)

  ///////////////////
  //
  const location = useLocation()
  const navigate  = useNavigate()
  const queryParams = new URLSearchParams(location.search);
  const redirect = queryParams.get('redirect');
  
  ////////////////////////
  //
  const [first_name, setFirstName] = useState('')
  const [last_name, setLastName] = useState('')
  const [username, setUsername] = useState('')
  const [password, setPassword] = useState('')
  const [confirm_password, setConfirmPassword] = useState('')

  const firstNameRef = useRef()
  const lastNameRef = useRef()
  const usernameRef = useRef()
  const passwordRef = useRef()
  const confirmPasswordRef = useRef()
  const usernameMessageRef = useRef()

  //////////////////
  //
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);


  const first_nameChangeHandler =(event)=>{
    let text = event.target.value
    if(isAlphabetical(text)){
      setFirstName(text)
      checkForm(text, last_name, password, confirm_password)
    }
  }

  const last_nameChangeHandler =(event)=>{
    let text = event.target.value
    if(isAlphabetical(text)){
      setLastName(text)
      checkForm(first_name, text, password, confirm_password)
    }
  }

  const passwordChangeHandler = (event)=>{
    let text = event.target.value
    setPassword(text)
    checkForm(first_name, last_name, text, confirm_password)
  }

  const confirm_passwordChangeHandler = (event)=>{
    let text = event.target.value
    setConfirmPassword(text)
    checkForm(first_name, last_name, password, text)
    
  }

  const usernameChangeHandler = (event)=>{
    let text = event.target.value
    if(isAlphaNumerical(text)){
      setError(null)
      setUsername(text)
        axios.get(`/api/checkUsername/?username=${text}`)
        .then( response =>{
          if (response.data[1] === ''){
            usernameRef.current.classList.remove('success') 
            usernameRef.current.classList.add('error')
            setIsRegisrationDisabled(true)
            usernameMessageRef.current.innerHTML = 'Username field cannot be empty'

          }
          else if (response.data[0]) {
            usernameRef.current.classList.add('success') 
            usernameRef.current.classList.remove('error')
            setIsRegisrationDisabled(false)
            usernameMessageRef.current.innerHTML = ''
          }else{
            usernameRef.current.classList.add('error')
            usernameRef.current.classList.remove('success')
            setIsRegisrationDisabled(true)
            usernameMessageRef.current.innerHTML = 'This username is taken'

          } 
        }).catch(error =>{
          setIsRegisrationDisabled(true)
          usernameMessageRef.current.innerHTML = 'BAD REQUEST 400'
          setError(error.response.data.detail || error.message)
        })
    }
  }

  /////////////////////////
  // refresh component when userData is updated
  const {userData, setUserData} =  useContext(UserContext)
  useEffect(()=>{
    if (userData.id){
      if (redirect != null){
        navigate(redirect)
      }else{
        navigate('/')
      }
    }
  }, [userData, navigate, redirect])
  
  const checkForm = (first_name='', last_name='', password='', confirm_password='')=>{
    setIsNextDisabled(true)
    setError(null)
    if (first_name === ''){
      firstNameRef.current.classList.add('error')
      firstNameRef.current.classList.remove('success') 
      lastNameRef.current.classList.remove('error')
      
    }else{
      firstNameRef.current.classList.add('success')      
      firstNameRef.current.classList.remove('error')
      if (last_name === ''){
        lastNameRef.current.classList.add('error')
        lastNameRef.current.classList.remove('success')
        passwordRef.current.classList.remove('error')
      }else{
        lastNameRef.current.classList.add('success')      
        lastNameRef.current.classList.remove('error')
        if (password.length < 8){
          passwordRef.current.classList.add('error')
          passwordRef.current.classList.remove('success') 
          confirmPasswordRef.current.classList.remove('error')
          confirmPasswordRef.current.classList.remove('success')

        }else{
          passwordRef.current.classList.add('success')      
          passwordRef.current.classList.remove('error')
          if (password !== confirm_password){
            passwordRef.current.classList.add('error')
            confirmPasswordRef.current.classList.add('error')
            confirmPasswordRef.current.classList.remove('success') 
          }else{
            confirmPasswordRef.current.classList.add('success')      
            confirmPasswordRef.current.classList.remove('error')
            setIsNextDisabled(false)
          }
    
        }
        
      }

    }

  }

  const submitHandler = async(event)=>{
    event.preventDefault()
    setLoading(true)
    try {    
      const response = await axios.post(
      '/api/user/register/',
      {
        first_name : first_name,
        last_name : last_name,
        password : password,
        username : username,
        confirm_password : confirm_password
      },
      {
        headers : {
          "Content-Type": "multipart/form-data",
        }
      }
      );
      const newUserData = {...response.data, loginDate:new Date()}
      localStorage.setItem('userData', JSON.stringify(newUserData))
      setUserData(newUserData)
      setLoading(false)
      setError(null)
      navigate( redirect || '/settings/profile/')

    } catch (error) {
      // Handle errors
      setError(error.response.data.detail || error.message)
      setLoading(false)
      console.log(error)

    }
  }
  const {Right, Left} = useContext(BrowserContext)
  const {translate} = useContext(TranslationContext)
  ////////////
  //
  const {setTitle, setDescription} = useContext(BrowserContext)
  useEffect(()=>{
    setTitle(translate('Register') + ` | ${translate('Ch7al Machya')}`)
    setDescription(translate('Register with us at ch7al machya and start selling vehicles.'))
  },[])
  return (
    <FormContainer className='mt-5 mw-700'>
            <h1>{translate('Register')}</h1>
              {error && <Message removeAlert={()=>(setError(null))} variant='danger' className='m-3'>{error}</Message>}
              <form className=''>

                <div className={`${step === 2 ? 'd-none' : 'd-flex'} flex-column`} >
                  <div className='d-flex gap-2' hidden={step === 2 ? 'hidden' : ''}>
                      <div ref={firstNameRef} className='mt-3 flex-grow-1 form-group'>
                          <label className='form-label'>{translate('First Name')}</label>
                          <input
                              type='text'
                              placeholder={translate('First Name')}
                              value={first_name}
                              onChange={first_nameChangeHandler}
                              maxLength={50}
                              className='form-control'
                          >
                          </input>
                      </div>
                      <div ref={lastNameRef} className='mt-3 flex-grow-1 form-group'>
                          <label className='form-label'>{translate('Last Name')}</label>
                          <input
                              type='text'
                              placeholder={translate('Last Name')}
                              value={last_name}
                              maxLength={50}
                              onChange={last_nameChangeHandler}
                              className='form-control'
                          >
                          </input>
                      </div>
                  </div>
                  <div  className='my-3 form-group' ref={passwordRef}>
                      <label className='form-label'>{translate('Password')} {0< password.length &&  password.length <8 ? `(${translate('Password must have at least 8 characters')})` : '' }</label>
                      <input
                          className='form-control'
                          type='password'
                          placeholder={translate('Password')}
                          value={password}
                          onChange={passwordChangeHandler}
                      >
                      </input>
                  </div>
                  <div className='mb-3 form-group' ref={confirmPasswordRef}>
                      <label className='form-label'>{translate('Confirm Password')} {password.length >=8 && (password !== confirm_password) ? `(${translate('Passwords do not match')})` : '' }</label>
                      <input
                          type='password'
                          placeholder={translate('Confirm Password')}
                          value={confirm_password}
                          onChange={confirm_passwordChangeHandler}
                          className='form-control'
                      >
                      </input>
                  </div>
                  <button type='submit' onClick={nextHandler} disabled={isNextDisabled} className='w-fit-content align-self-end d-flex btn btn-success'>
                    <span>{translate('Next')} <i className={`fa-solid fa-chevron-${ Right }`}></i></span>
                  </button> 
                </div>
                
                <div className={`${step === 1 ? 'd-none' : 'd-flex'} flex-column`} hidden={step === 1 ? true : false}>
                  <div className='my-3 form-group' ref={usernameRef} >
                    <label className='form-label'>{translate('Username (Latin letters only)')}</label>
                    <input 
                      className='form-control'
                        type='username'
                        placeholder={translate('Username')}
                        value={username}
                        onChange={usernameChangeHandler}
                        maxLength={50}
                    />
                    <label className='mt-2' ref={usernameMessageRef}></label>
                  </div>
                  <div className='d-flex gap-3 justify-content-between'>
                    <button type='submit'  className='w-fit-content align-self-end d-flex btn btn-success' onClick={prevHandler}>
                      <span><i className={`fa-solid fa-chevron-${Left}`}></i> {translate('Go back')}</span>
                    </button>
                    <button type='submit' disabled={isRegistrationDisabled} onClick={submitHandler} className='w-fit-content align-self-end d-flex btn btn-success'>
                      <span>{translate('Register')}</span> { loading ? <StyledSpan><Loader diam={23}/></StyledSpan> : ''}
                    </button>
                  </div>
                </div>
                  
                
                
              </form>

              <div className='row'>
                  <div className='mt-3 col'>
                      {translate('Do you have an account?')} <Link  className='color-theme' to={redirect !== null ? '/login/?redirect=' + redirect : '/login/'} >{translate('Login')}</Link>
                  </div>
              </div>

    </FormContainer>
  )
}

export default RegisterScreen